.cart {
  position: relative;
  border: 1px solid #ccc;
  transform: scaleY(0.98);
}
.amount span {
  color: crimson;
  padding: 0 20px;
}
.amount button {
  width: 40px;
  height: 40px;
  border: 1px solid #777;
}
.delete {
  position: absolute;
  top: 0;
  right: 5px;
  color: crimson;
  font-weight: 900;
  cursor: pointer;
}
.total {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.total h3 {
  color: crimson;
}
.total > button {
  margin-left: 10px;
  background-color: red;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 20px;
}
.total > a {
  background: rgba(63, 60, 154, 0.9);
  width: fit-content;
  border-radius: 15px;
  color: white;
  margin-top: 10px;
  padding: 10px 20px;
  display: inline-block;
  text-transform: uppercase;
}
.total > a:hover {
  color: #ffff !important;
}
.none-product {
  text-align: 'center';
  font-size: '2rem';
}
