a:hover {
  text-decoration: none;
  /* color: #524EB7; */
}

label {
  font-size: 14px;
}

input {
  padding: 14px 12px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  box-sizing: border-box;
  outline: 0;
  transition: all ease-in-out 0.3s;
  font-size: 14px;
  margin: 10px 0px;
}



legend {
  font-weight: 500;
  color: #000;
  padding: 0.6180469716em 1em;
  width: 38%;
}

.header-information {
  background-color: #f9f9f9;
  padding: 40px 0;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  align-content: flex-start;
  text-align: center;
}

.header-label {
  font-weight: 700;
  color: #000;
  font-size: 32px;
  line-height: 42px;
}

.header-direction a {
  color: #727272;
}

.body-information {
  padding: 5% 32%;
}

.name-information {
  display: flex;
  justify-content: space-between;
}

.password-change {
  padding: 1.618em;
  padding-top: 1em;
  margin: 0;
  margin-bottom: 1.618em;
  margin-top: 1.618em;
  border: 1px solid silver;
}

.btn-submit {
  display: flex;
  align-items: center;
  border: none;
  background: #524eb7;
  color: #fff;
  cursor: pointer;
  padding: 25px 45px;
  font-size: 14px;
  line-height: 24px;
  border-radius: 30px;
}

.detail-user-box {
  /* display: block; */

  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  display: flex;
}

.user-box {
  text-align: center;
  background-color: #ccc;
  border-radius: 10px;
  max-height: 70px;
  width: 300px;
  margin: 6px;
  /* height: 100%; */
}

.user-box>p {
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0;
  opacity: 0.8;
}

.user-box p>a {
  color: #000 !important;
}