.login-page {
  max-width: 500px;
  /* border: 2px solid  #524eb7; */
  border-radius: 20px;
  padding: 30px;
  margin: 50px auto;
  box-shadow: 0px 4px 4px #524eb7;
  /* background-color: #524eb7; */
}
.login-page h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #555;
}
.login-page form input {
  border-color: rgba(0, 0, 0, 0.6);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-width: 0px 1px 1px 1px;
  border-style: solid;
  background: #ffffff;
}
.login-page form input,
.login-page form button {
  width: 100%;
  height: 40px;
  margin: 10px 0;
  padding: 0 5px;
  outline: rgb(3, 165, 206);
  border-radius: 19px;
}
.login-page form .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login-page form button {
  width: 150px;
  background: #524eb7;
  color: white;
  border-radius: 19px;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.login-page form a {
  color: orange;
  letter-spacing: 1.3px;
  text-transform: uppercase;
}
.text-desc {
  font-size: 9px;
}
.text-email {
  font-weight: 400;
  font-size: 13px;
  line-height: 14px;
}
.infor {
  padding: 20px;
}
.info-section input {
  padding: 10px;
  width: 100%;
}
.info-section button {
  background-color: #07055b;
  color: white;
  width: 100px;
  margin-top: 10px;
}

#col-1 {
  position: relative;
  width: 50%;
  float: left;
  height: 100%;
}
#col-2 {
  position: relative;
  width: 50%;
  float: left;
  height: 100%;
}

.login-row {
  position: relative;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
