.product_card {
  width: 308px !important;
  overflow: hidden;
  height: fit-content;
  padding: 17px;
  box-shadow: 0 0 15px #ddd;
  margin: 22px 19px;
  position: relative;
}
.product_card > img {
  width: 100%;
  height: 242px;
  display: block;
  object-fit: cover;
}
.product_box > h2 > a {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  white-space: nowrap;
  text-transform: capitalize;
  cursor: pointer;
  color: #323232;
}

.product_box > span {
  color: crimson;
}
.product_box > p {
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  height: 70px;
  overflow: hidden;
  color: #323232;
}
.row_btn {
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.row_btn svg {
  width: 339px !important;
  height: 32px;
}
.row_btn a {
  width: 84px;
  height: 33px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(247, 102, 49, 1);
  font-weight: 600;
  letter-spacing: 2px;
  padding: 6px;
}
#btn_buy {
  position: relative;
  /* bottom: 42px; */
  /* left: 189px; */
  display: flex;
  justify-content: center;
  align-items: center;
}
#btn_view {
  border-radius: 26px;
  /* position: relative; */
  /* bottom: 487px; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.product_card input {
  position: absolute;
  width: 25px;
  height: 25px;
}
.price {
  color: rgba(247, 102, 49, 1);
  width: 90px;
  height: 30px;
  padding-top: 3px;
  text-align: center;
  border-radius: 26px;
  font-weight: 650;
  background: linear-gradient(
      0deg,
      rgba(179, 60, 16, 0.1),
      rgba(179, 60, 16, 0.1)
    ),
    rgba(247, 102, 49, 0.1);
}
.content {
  animation-play-state: paused;
}
.animation:hover {
  width: auto;
  overflow: hidden;
  white-space: nowrap;
  box-sizing: border-box;
  animation: animation 5s linear infinite;
}
@keyframes animation {
  0% {
    text-indent: 50px;
  }
  100% {
    text-indent: -300px;
  }
}
